.navbar{
    width: 100%;
    background-color:#333;
}
.menu-container{
    display: flex;
    align-items: center;
    justify-content: start;
    padding: 10px;
}
.nav-links{
    list-style: none;
    display: flex;
    align-items: center;
    margin-left: 20px;

}
.nav-link{
color: white;
text-decoration: none;
margin-right:15px;
}
.nav-link:hover{
color: grey;
}
.nav-link:focus{
color: #61dafe;
background-color: #555;
}
.nav-links li{
    margin: 10px 0;
}